
#portfolio.articles_home_content {
  margin: -30px;
}

#portfolio.articles_home_content .portfolio-item {
  padding-bottom: 15px;
}

#portfolio.articles_home_content .view_all {
  padding: 0 0 30px 40px;
}

#portfolio.articles_home_content .cbp-item{
  padding: 15px !important;
}

#portfolio.articles_home_content section {
  padding-left: 15px !important;
  padding-right: 15px !important;
}

#portfolio.articles_home_content .cbp-item-wrapper {
  min-height: 400px;
  border-radius: 6px;
  -webkit-border-radius:6px;
  -moz-border-radius: 6px;
  display: inline-block;
  overflow: hidden;
  box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
  -webkit-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
  -moz-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
/*    transition: all ease 0.3s;
  -webkit-transition: all ease 0.3s;
  -moz-transition: all ease 0.3s;*/
}

.content-header{
  z-index: 5;
}

#portfolio.articles_home_content .cbp-item-wrapper:hover {
  transition: all ease 0.3s;
  -webkit-transition: all ease 0.3s;
  -moz-transition: all ease 0.3s;
  box-shadow: 0px 0px 20px 0px rgba(4, 6, 4, 0.08);
  -webkit-box-shadow: 0px 0px 20px 0px rgba(4, 6, 4, 0.08);
  -moz-box-shadow: 0px 0px 20px 0px rgba(4, 6, 4, 0.08);
}

#portfolio.articles_home_content .cbp-item figure img, .work figure{
  width: 100%;
}

#portfolio.articles_home_content .cbp-item figcaption .title{
  font-size: 14px;
  color: #000;
}

#portfolio.articles_home_content .cbp-item figcaption{
  padding: 14px;
}

#portfolio.articles_home_content figcaption .info{
  font-size: 13px;
  color: #2d2d2d;
  line-height: 18px;
  display: block;
  padding: 10px 0;
}

#portfolio.articles_home_content .cbp-item:hover figure .icon{
  opacity: 0.9;
  transition: all ease 0.3s;
  -webkit-transition: all ease 0.3s;
  -moz-transition: all ease 0.3s;
  transform: translate(0,-25px);
  -webkit-transform: translate(0,-25px);
  -moz-transform: translate(0,-25px);
}


#portfolio.articles_home_content .cbp-item figure .icon {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  opacity: 0;
  display: table-cell;
  vertical-align: middle;
  top: 50%;
  margin-top: -20px;
  transition: all ease 0.3s;
}

#portfolio.articles_home_content .cbp-item figure .icon i{
  color: #fff;
  padding: 17px 0 16px;
  min-width: 50px;
  border-radius: 50px;
  text-align: center;
  transition: all ease 0.3s;
  text-align: center;
  background: rgba(3, 122, 249, 0.80);
}

#portfolio.articles_home_content .cbp-item figure .icon i:before{
  font-size: 18px;
}
