:not(pre) > code[class*="language-"], pre[class*="language-"] {
    background: #fbfaf9;
}

.article-title{
  font-size: 26px;
  font-weight: bold;
  line-height: 30px;
}

#article {
  padding: 0 15px;
}

#article #content {
  margin: 15px;
}

#article .post .post-content p {
  margin-top: 2em;
  line-height: 32px;
  font-size: 21px;
}

#article .post .post-content h3 {
  margin-top: 2em;
  font-weight: 600;
}

#article .post .article-image {
  width: 100%;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  overflow: hidden;
}

#article .post .post-content ul {
  margin-top: 1.5em;
  line-height: 32px;
  font-size: 21px;
}

#article .post .post-content blockquote { 
  display: flex;
  flex-direction: row;
}

#article .post .post-content blockquote p {
  padding-top: 0;
  margin-top: 0;
  font-weight: 400;
  margin-bottom: 0;
  padding-left: 20px;
}

#article .post .post-content blockquote::before {
  content: '"';
  font-size: 50px;
  padding-top: 15px;
  font-style: italic;
  padding-left: 15px;
  padding-right: 15px;
}

#article .post .article-image img{
  width: 100%;
}

#article .post .article-iframe iframe{
  width: 100%;
}

#article .post{
  padding: 20px 50px;
}

#article .post .post-date {
  color: #999999;
}

#article nav {
  margin-bottom: 10px;
}